:root {
  --am-map-height: 650px;
  --am-list-width: 0;
  --color-theme-green: #21bf73;
  --color-theme-blue: #41a7b3; }

.wp-block-mapped-posts-archive,
.page-content > .wp-block-mapped-posts-archive {
  padding-right: 0;
  padding-left: 0;
  width: 100%;
  max-width: 100%; }

#mapped-posts-data {
  display: none; }

.archive-map-wrapper {
  position: relative;
  border-top: 2px solid #e3e3e3;
  border-bottom: 2px solid #e3e3e3; }
  .archive-map-wrapper .archive-filter-list {
    position: absolute;
    z-index: 200;
    top: 2rem;
    left: 2rem;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.25);
    border: 1px solid var(--color-theme-grey-med-light);
    background-color: #f1f1f1;
    padding: 1rem 2rem;
    font-size: 16px;
    font-weight: 500; }
    .archive-map-wrapper .archive-filter-list ul {
      list-style: none;
      margin: 0;
      padding: 0; }
    .archive-map-wrapper .archive-filter-list li {
      display: flex;
      align-items: center;
      line-height: 1.75; }
    .archive-map-wrapper .archive-filter-list section > ul {
      padding-bottom: 1rem;
      border-bottom: 1px solid var(--color-theme-grey-med-light); }
    .archive-map-wrapper .archive-filter-list section:last-of-type > ul {
      margin-bottom: 1rem; }
    .archive-map-wrapper .archive-filter-list input[type="checkbox"] {
      -webkit-appearance: none;
      background-color: #fff;
      border: 1px solid var(--color-theme-grey-med-light);
      height: 20px;
      width: 20px;
      border-radius: 50%;
      display: inline-block;
      position: relative;
      margin-right: 5px; }
      .archive-map-wrapper .archive-filter-list input[type="checkbox"]:checked {
        background-color: var(--color-theme-orange); }
        .archive-map-wrapper .archive-filter-list input[type="checkbox"]:checked:after {
          content: '\e80a';
          font-family: var(--font-family-icons);
          font-size: 14px;
          position: absolute;
          top: -3px;
          left: 3px;
          color: #fff; }
    .archive-map-wrapper .archive-filter-list button {
      background-color: transparent;
      color: var(--color-theme-grey-dark);
      border: none;
      padding-left: 0; }
      .archive-map-wrapper .archive-filter-list button:after {
        content: '\e805';
        font-family: var(--font-family-icons);
        display: inline-block;
        margin-left: 5px; }
      .archive-map-wrapper .archive-filter-list button.btn-active:after {
        content: '\e804'; }
  .archive-map-wrapper .leaflet-container {
    width: 100%;
    height: var(--am-map-height); }
  .archive-map-wrapper .data-list {
    position: absolute;
    width: 100%;
    z-index: 500;
    background-color: #fff;
    padding: 1rem 1.5rem;
    transition: height 0.5s ease, top 0.3s ease, left 0.3s ease, width 0.3s ease;
    font-weight: var(--font-weight-medium); }
    .archive-map-wrapper .data-list .data-list--data {
      padding-left: 41px;
      margin: 0; }
      .archive-map-wrapper .data-list .data-list--data li {
        font-size: 20px;
        cursor: pointer;
        color: #727272; }
        .archive-map-wrapper .data-list .data-list--data li.selected {
          color: var(--color-theme-green); }
        .archive-map-wrapper .data-list .data-list--data li:hover {
          color: var(--color-theme-blue); }
        .archive-map-wrapper .data-list .data-list--data li h2 {
          font-size: 20px;
          margin-bottom: 0; }
        .archive-map-wrapper .data-list .data-list--data li .post-excerpt {
          font-weight: 400;
          font-size: 15px;
          line-height: 1.25; }
  .archive-map-wrapper .data-list--header {
    color: var(--color-theme-green);
    font-size: 18px;
    cursor: pointer;
    display: flex;
    justify-content: space-between; }
    .archive-map-wrapper .data-list--header:after {
      content: '\e806';
      font-family: var(--icons-font-family); }
  .archive-map-wrapper.list-visible .data-list {
    top: 0;
    right: 0;
    bottom: 0;
    height: var(--am-map-height); }
    .archive-map-wrapper.list-visible .data-list ol {
      height: calc(var(--am-map-height) - 6rem);
      overflow-y: auto;
      transition: all 0.3s ease; }
  .archive-map-wrapper.list-visible .data-list--header {
    padding: 0.5rem 0;
    margin-bottom: 0.5rem;
    border-bottom: 2px solid var(--color-border); }
    .archive-map-wrapper.list-visible .data-list--header:after {
      content: '\e807'; }
  .archive-map-wrapper.list-hidden .data-list {
    top: 10px;
    right: 10px;
    height: 50px;
    padding: 0.5rem 1rem;
    width: calc(var(--am-list-width) - 30px);
    border: 1px solid var(--color-border); }
    .archive-map-wrapper.list-hidden .data-list ol {
      display: none; }
  .archive-map-wrapper.list-hidden .data-map {
    width: 100%;
    height: 100%; }

@media screen and (max-width: 599px) {
  .archive-map-wrapper.list-visible .leaflet-control-container {
    display: none; } }

@media screen and (min-width: 600px) {
  .archive-map-wrapper .data-list {
    width: var(--am-list-width); }
  .archive-map-wrapper.list-visible .data-map {
    width: calc(100% - var(--am-list-width));
    margin-right: var(--am-list-width); } }

.leaflet-marker-icon .count {
  position: absolute;
  left: 7px;
  top: 6px;
  font-weight: bold;
  display: block;
  text-align: center;
  width: 16px; }

.leaflet-marker-icon.has-shadow > span:after {
  content: '';
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.3);
  display: block;
  height: 3px;
  width: 20px;
  border-radius: 100%;
  transform: rotate(-15deg);
  position: absolute;
  left: 17px;
  bottom: -7px; }

.leaflet-popup-content-wrapper {
  border-radius: 0; }
  .leaflet-popup-content-wrapper .map-archive-popup {
    width: 215px;
    font-family: var(--global-font-family);
    color: var(--global-font-color); }
    .leaflet-popup-content-wrapper .map-archive-popup h2 {
      font-size: 18px; }
    .leaflet-popup-content-wrapper .map-archive-popup img {
      width: 100%;
      height: auto; }
    .leaflet-popup-content-wrapper .map-archive-popup .popup-title {
      display: block;
      color: var(--color-theme-green);
      font-size: 18px;
      font-weight: 900;
      text-align: center;
      margin-bottom: 10px; }
    .leaflet-popup-content-wrapper .map-archive-popup .popup-link {
      display: block;
      color: var(--color-theme-green);
      font-size: 14px;
      font-weight: 900;
      text-align: center;
      margin-top: 10px; }
    .leaflet-popup-content-wrapper .map-archive-popup .wp-block-mapped-posts-archive__text-link {
      font-size: 18px;
      padding-top: 0.25rem;
      padding-bottom: 0.25rem;
      font-weight: var(--font-weight-bold);
      display: block; }
    .leaflet-popup-content-wrapper .map-archive-popup a.wp-block-mapped-posts-archive__text-link {
      color: var(--color-theme-green); }
      .leaflet-popup-content-wrapper .map-archive-popup a.wp-block-mapped-posts-archive__text-link:hover {
        color: var(--color-theme-green-light-links); }

.marker-cluster-small {
  background-color: rgba(204, 93, 27, 0.6); }

.marker-cluster-small div {
  background-color: rgba(204, 93, 27, 0.6); }

.marker-cluster-medium {
  background-color: rgba(204, 93, 27, 0.6); }

.marker-cluster-medium div {
  background-color: rgba(204, 93, 27, 0.6); }

.marker-cluster-large {
  background-color: rgba(97, 144, 44, 0.6); }

.marker-cluster-large div {
  background-color: rgba(97, 144, 44, 0.6); }

.marker-cluster div {
  color: #fff;
  font-family: var(--global-font-family); }

.archive-map-wrapper .leaflet-bar {
  border-radius: 0;
  box-shadow: none;
  border: 1px solid var(--color-border); }
  .archive-map-wrapper .leaflet-bar a {
    border-radius: 0 !important; }

.archive-map-wrapper .leaflet-bottom {
  z-index: 400; }

.archive-map-wrapper .leaflet-pane {
  z-index: unset; }
  .archive-map-wrapper .leaflet-pane.leaflet-popup-pane {
    z-index: 700; }
  .archive-map-wrapper .leaflet-pane.leaflet-marker-pane {
    z-index: 600; }

.editor-styles-wrapper .wp-block-ck-custom_posttype__list > ul {
  list-style: none;
  padding-left: 0; }
